@import '../../constants/constants.scss';
.scanner {
  display: flex;
  flex-direction: column;
  height: 100vh;

  .mb-10 {
    margin-bottom: 10px;
  }

  &__error {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100vh;

    &--qr {
      height: 10vh;
    }

    &--invalid {
      &__qr {
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 16px;
        color: $color-red;
        text-align: center;
      }

      &__platform {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        color: $color-light-gray;
      }
    }
  }

  hr {
    border: 0.5px solid $color-gray;
    margin: 0;
  }

  &__divider {
    border: 8px solid $color-lighter-white;
    margin-bottom: 16px;
  }

  &__information {
    padding-left: 16px;

    &--instance {
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      margin-bottom: 16px;

      &__name {
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
        margin-top: 0;
        margin-bottom: 2px;
      }

      &__value {
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        color: $color-darker-gray;
        margin-top: 0;
        margin-bottom: 16px;
      }
    }
  }

  &__operation {
    padding: 0 16px;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    margin-bottom: 16px;

    &--history {
      margin: 0;
      font-weight: 700;
      font-size: 14px;
      line-height: 19px;
      color: $color-blue;
    }
  }

  &__history {
    padding: 0 16px;

    &--information {
      font-weight: 700;
      font-size: 12px;
      line-height: 16px;
      color: $color-darker-gray;
      margin: 0;
      margin-bottom: 16px;
    }
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    color: $color-black;
    margin: 0 0 16px 16px;
  }

  &__image {
    height: 100%;

    &--container {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 30vh;
      margin-bottom: 20px;
    }
  }
}
