@import '../../../constants/constants.scss';
.scanner-error {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100vh;

  &__qr {
    margin-bottom: 21px;
    height: 10vh;
  }

  &__invalid {
    &--qr {
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 16px;
      color: $color-red;
      text-align: center;
      margin: 0;
    }

    &--platform {
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      text-align: center;
      color: $color-light-gray;
      padding: 0 50px;
    }

    // }
  }

  &__camera-disabled {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0 24px;
    padding: 30px 40px;
    background: $color-lighter-black;
    border-radius: 10px;

    &--title {
      font-weight: 700;
      font-size: 18px;
      line-height: 21px;
      text-align: center;
      color: $color-white;
      margin-top: 0;
      margin-bottom: 21px;
    }

    &--description {
      font-weight: 400;
      font-size: 15px;
      line-height: 16px;
      color: $color-white;
    }
  }
}
