@import '../../../constants/constants.scss';

.scanner__header {
  padding-bottom: 16px;

  &--button {
    margin: 10px 8px 0 8px;
    background: $color-white;
    border: 0;
    padding: 0;
    cursor: pointer;

    &__arrow-white {
      color: $color-white;
    }

    &__arrow-blue {
      color: $color-blue;
    }
  }
}
